import wallet from "@/common/js/wallet.js";
export default {
	data() {
		return {
			pageHide: false,
			wallet: {}
		}
	},
	onNavigationBarButtonTap(e) {
		if(e.index == 0){
			uni.navigateTo({
				url: '../message/message'
			})
		}
	},
	onHide() {
		this.pageHide = true;
	},
	onShow() {
		if(this.pageHide){
			this.pageHide = false;
			this.wallet = this.$Wallet.getCurrentWallet();
		}
	},
	onLoad() {
		this.wallet = this.$Wallet.getCurrentWallet();
	},
	methods: {
		
	}
}
